<template>
  <div class="fxHost">
    <div class="fxGrid">
      <FxCell v-for="(fx) in Fx_chain1" :chainId="fx.chainId" :id="fx.id" :item="fx" ></FxCell>
      <FxCell v-for="(fx) in Fx_chain2" :chainId="fx.chainId" :id="fx.id" :item="fx"></FxCell>
      <FxCell v-for="(fx) in Fx_chain3" :chainId="fx.chainId" :id="fx.id" :item="fx"></FxCell>
      <FxCell v-for="(fx) in Fx_chain4" :chainId="fx.chainId" :id="fx.id" :item="fx" @fxMounted="this.initFxConnections()"></FxCell>
    </div>
    <div class="gainList">
      <div id="gain1" class="channelGain"></div>
      <div id="gain2" class="channelGain"></div>
      <div id="gain3" class="channelGain"></div>
      <div id="gain4" class="channelGain"></div>
    </div>
  </div>
</template>

<script>
import FxCell from '../Effects/FxCell.vue'
import {Gain, getDestination} from "tone";
export default {
  name: "FxHost",
  components: {FxCell},

  data(){
    return{
      Fx_chain1: [],
      Fx_chain2: [],
      Fx_chain3: [],
      Fx_chain4: []
    }
  },

  mounted() {
    // note: refs are kept incremental through the chains, while everything else is indexed relatively to the chain
    console.log("creating fx")
    for (let i = 1; i <= this.nFxXchain; i++) {
      this.Fx_chain1.push({id: i, ref: 'Fx' + i, chainId: 0});
      this.Fx_chain2.push({id: i, ref: 'Fx' + i + this.nFxXchain, chainId: 1});
      this.Fx_chain3.push({id: i, ref: 'Fx' + i + this.nFxXchain * 2, chainId: 2});
      this.Fx_chain4.push({id: i, ref: 'Fx' + i + this.nFxXchain * 3, chainId: 3});
      // console.log(i)
    }

    this.fxChains[0][0] = new Gain(1);
    for (let i = 1; i < this.nFxChains; i++) {
      this.fxChains[i][0] = new Gain(0);
    }
  },

  methods:{
    // -------------------- AUDIO -----------------------
    initFxConnections(){
      for (let i = 0; i < this.nFxChains; i++) {
        for (let j = 0; j < this.nFxXchain; j++) {
          this.fxChains[i][j].connect(this.fxChains[i][j+1].getToneReference())
          // console.log("chain ", i ," id: ", j, " into chain ", i , " id: ", j+1)
        }
        this.fxChains[i][this.nFxXchain].connect(getDestination()) // todo: connect to Master Gain node?
        // console.log("chain ", i, " id: ", this.nFxXchain-1, " into getDestination()")
      }
    },

    // first node of each chain is a gain node - we set its volume to adjust whole chain gain
    setChainVolume(chainId, volume){
      this.fxChains[chainId][0].gain.rampTo(volume, 0.1)

    }
  }
}

</script>

<style scoped>
  .fxHost{
    background-color:#fdffb6;
    display: flex;
    opacity: 90%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .fxGrid{
    height: 97%;
    width: 100%;
    flex: 1;
    display: grid;
    grid-column: auto;
    grid-template-rows:  1fr 1fr 1fr 1fr 1fr ;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-auto-flow: column;
    grid-gap: 2%;
    margin: 3%;
  }

  .gainList{
    height: 16%;
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin:10px;
    align-items: center;
    flex-direction: row;
    background-color: lightgoldenrodyellow;
    border: .1vw solid black;
  }

  .channelGain {
    height: 80%;
    aspect-ratio: 1;
    border-radius: 50%;
    background-color: #3b210e;
  }

</style>




