<template>
  <div class="modHost">
    <template
      v-for="modulator in modulatorList"
      :key="modulator.id"
      :ref="modulator.ref"
    >
      <Envelope
        @dragging="handleDragging"
        :id="modulator.id"
        ref="Modulators"
      />
    </template>
    <ModSelector
      :nModulators="this.nModulators"
      @selection="changeSelection"
      @dragging="handleDragging"
      ref="EnvSelector"
    />
  </div>
</template>

<script>
import * as Tone from "tone";
import Envelope from "./Envelope.vue";
import ModSelector from "./ModSelector.vue";
import { isProxy, toRaw } from 'vue';

export default {
  components: {
    Envelope,
    ModSelector,
  },
  data() {
    return {
      name: "modHost",
      //number of total modulators
      nModulators: 6,
      //visible modulator
      selection: 0,
      //list of modulators
      modulatorList: [],
    };
  },
  created() {
    //create array of modulators
    for (let i = 0; i < this.nModulators; i++) this.modulatorList.push({ id: i, ref: i });
    //console.log(toRaw(this.modulatorList));
  },
  mounted() {
    //unhide selected modulator
    toRaw(toRaw(this.$refs.Modulators)[this.selection]).unHide();
  },
  methods: {
    handleMouseUp(e){
      toRaw(toRaw(this.$refs.Modulators)[this.selection]).handleMouseUp(e);
    },
    handleMouseMove(e){
      toRaw(toRaw(this.$refs.Modulators)[this.selection]).handleMouseMove(e);
    },
    /*
    noteDown(note) {
      this.modulatorList.forEach((modulator) => {
        //console.log(this.$refs.Modulators);
        toRaw(toRaw(this.$refs.Modulators)[this.selection]).noteDown(note);
      });
    },
    noteUp(note) {
      this.modulatorList.forEach((modulator) => {
        toRaw(toRaw(this.$refs.Modulators)[this.selection]).noteUp(note);
      });
    },
    */
    changeSelection(id) {
      console.log("selected "+id)
      console.log(toRaw(toRaw(this.$refs.Modulators)[this.selection]))
      
      toRaw(toRaw(this.$refs.Modulators)[this.selection]).hide();
      
      this.selection = id;
      console.log("selected " + this.selection);
      toRaw(toRaw(this.$refs.Modulators)[this.selection]).unHide();
      
    },
    handleDragging(draggedId) {
      //handles @dragging on ModSelector

      //------DATA ROUTING------
      //creating path with id of dragged modulator
      let path = [draggedId]
      //inserting actual module in path (at top)
      path.unshift(this.name);
      //---------------

      let modType = toRaw(toRaw(this.$refs.Modulators)[draggedId]).type

      let modParam = toRaw(toRaw(toRaw(this.$refs.Modulators)[draggedId]).parameters);
      this.$emit("dragging",{ type:"dragging", path: path, modType: modType, params: modParam } );
      //console.log(path, modParam)
    },
  },
};
</script>

<style scoped>
.modHost {
  grid-column: span 2; /* takes 2 spaces horizontally*/
  background-color: #9bf6ff;
  position: relative;
  display: grid;
  grid-template-rows: 80% 20%;
}

.env_background {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.env_canvas {
  height: 0;
  width: 0;
  left: 1%;
  background-color: #cdfbff;
  border-radius: 0.2vw;
  border: 0.1vw solid #141f20;
  box-sizing: border-box;
}
</style>