<template>
    <div class="buttonList">
        <div
        v-for="button in buttonList"
        :key="button.id"
        :ref="button.ref"
        class="button"
        
      >
        <div class="identifier" @click="handleClick(button.id)" >{{ button.id }}</div>
        <div class="draggableArea" @mousedown="handleMouseDown(button.id)">X</div>
      </div>
    </div>
      
</template>

<script>
import * as Tone from "tone";

export default {
  props: ["nModulators"],
  data() {
    return {
      buttonList: [],
    };
  },

  mounted() {
    for (let i = 0; i < this.nModulators; i++) this.buttonList.push({ id: i, ref: "button" + i });
  },

  methods: {
    handleClick(id) {
      this.$emit("selection", id);
    },
    handleMouseDown(id) {
      //handles dragging on draggable area of selection
      this.$emit("dragging", id);
    },
  },
};
</script>

<style scoped>
.button {
    display:grid;
    grid-template-columns: 60% 40%;
    flex:1;
    width: 100%;
    height: 100%;
    border: 0.2vw solid black;
    border-radius: 0.5vw;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}
.buttonList{
    display: flex;
    flex-basis: column;
    justify-content: center;
    align-items: center;
}
.identifier{
  border-radius: 0.5vw;
  border: 0.1vw solid black;
  box-sizing: border-box;
}
.draggableArea{
  border-radius: 0.5vw;
  border: 0.1vw solid black;
  box-sizing: border-box;
}
</style>