<template>
  <div ref="container" class="container hidden">
    <div class="envelope_container">
      <div class="envelope" :style="this.adsr">
        <div
          class="attack_area"
          :style="attack_area"
          @mousedown="attack_down()"
        ></div>
        <div
          class="attack_dot"
          :style="attack_dot"
          @mousedown="attack_down()"
        ></div>
        <div
          class="decay_area"
          :style="decay_area"
          @mousedown="decay_down()"
        ></div>
        <div
          class="decay_dot"
          :style="decay_dot"
          @mousedown="decay_down()"
        ></div>
        <div
          class="sustain_area"
          :style="sustain_area"
          @mousedown="sustain_down()"
        ></div>
        <div
          class="sustain_line"
          :style="sustain_line"
          @mousedown="sustain_down()"
        ></div>
        <div
          class="release_area"
          :style="release_area"
          @mousedown="release_down()"
        ></div>
        <div
          class="release_dot"
          :style="release_dot"
          @mousedown="release_down()"
        ></div>

        <div class="attack" :style="this.attack">
          <div class="attack_color" :style="this.attack_Color"></div>
        </div>
        <div class="decay" :style="this.decay">
          <div class="decay_head" :style="this.decay_head">
            <div class="decay_color" :style="this.decay_Color"></div>
          </div>
          <div class="decay_color" :style="this.decay_Color"></div>
        </div>
        <div class="sustain" :style="this.sustain">
          <div class="sustain_head" :style="this.sustain_head"></div>
          <div class="sustain_color" :style="this.sustain_Color"></div>
        </div>
        <div class="release" :style="this.release">
          <div class="release_color" :style="this.release_Color"></div>
        </div>
        <!--
        <div class="space" :style="this.space"></div>
        -->
        <div class="time_strech" :style="this.time_Stretch"></div>
      </div>
    </div>

    <div class="knobs">
      <div v-for="knob in knobList" :key="knob.id" :ref="knob.ref" class="knob">
        {{ knob.id }}
      </div>
    </div>
  </div>
</template>

<script>
import { transcode } from "buffer";
import * as Tone from "tone";
import ToolsHost from "../Tools/ToolsHost.vue";
const duration = 2.5;
const colors = {
  red: { light: "lightcoral", medium: "red", dark: "darkred", glow: "pink" },
  yellow: { light: "yellow", medium: "orange", dark: "gold", glow: "" },
  green: {
    light: "lightgreen",
    medium: "olive",
    dark: "darkgreen",
    glow: "aqua",
  },
  gray: { light: "lightgray", medium: "darkgray", dark: "black", glow: "" },
  blue: {
    light: "lightblue",
    medium: "deepskyblue",
    dark: "darkblue",
    glow: "cyan",
  },
};
export default {
  props: ["id"],
  data() {
    return {
      tempEvent: null,
      type: "envelope",
      parameters: {},
      knobList: [],
      is_attack_down: false,
      is_decay_down: false,
      is_sustain_down: false,
      is_release_down: false,
    };
  },
  mounted() {
    this.parameters = {
      attack: 0.1,
      attackCurve: "linear",
      decay: 0.5,
      decayCurve: "linear",
      sustain: 0.5,
      releaseCurve: "linear",
      release: 1,
      scale: 1,
    };
    for (let i = 0; i < 9; i++) {
      this.knobList.push({ id: i, ref: "knob" + i });
    }

    setTimeout(() => {
      this.synths.list.forEach((synth) => {
        synth.setModulatorParameters("envelope", "vol", this.parameters);
      });
    }, 500);

    //this.connectEnvelope("all osc");
  },
  methods: {
    unHide() {
      this.$refs.container.classList.remove("hidden");
    },
    hide() {
      this.$refs.container.classList.add("hidden");
    },
    handleMouseMove(e) {
      //console.log(e)
      //NOT NECESSAARY, FOR DEBUGGING
      if (this.isDragging) {
        this.tempEvent = e;
      }
      let p = this.parameters;
      if (this.is_attack_down) { // attack
        if (e.movementX > 0) {
          console.log(
            this.parameters.attack,
            duration * p.scale - (p.attack + p.release + p.sustain)
          );
          p.attack = Math.min(
            p.attack + e.movementX / 100,
            duration * p.scale - (p.decay + p.release + p.sustain)
          );
        }
        if (e.movementX < 0) {
          console.log(p.attack);
          p.attack = Math.max(p.attack + e.movementX / 100, 0);
        }
      }
      if (this.is_decay_down) { // decay
        if (e.movementX > 0) {
          console.log(this.parameters.decay);
          p.decay = Math.min(
            p.decay + e.movementX / 100,
            duration * p.scale - (p.attack + p.release + p.sustain)
          );
        }
        if (e.movementX < 0) {
          console.log(p.decay);
          p.decay = Math.max(p.decay + e.movementX / 100, 0);
        }
      }
      if (this.is_sustain_down) { // sustain
        if (e.movementY > 0) {
          p.sustain = Math.max(p.sustain - e.movementY / 100, 0);
        } else if (e.movementY < 0) {
          p.sustain = Math.min(p.sustain - e.movementY / 100, 1);
        }
        console.log(p.sustain);
      }
      if (this.is_release_down) { // release
        if (e.movementX > 0) {
          p.release = Math.min(
            p.release + e.movementX / 100,
            duration * p.scale - (p.attack + p.decay + p.sustain)
          );
        }
        if (e.movementX < 0) {
          p.release = Math.max(p.release + e.movementX / 100, 0);
        }
      }
      if (
        this.is_attack_down ||
        this.is_decay_down ||
        this.is_sustain_down ||
        this.is_release_down
      ) {
        this.synths.list.forEach((synth) => {
          synth.setModulatorParameters("envelope", "vol", this.parameters);
        });
      }
    },
    handleMouseUp() {
      /*if (this.isDragging) {
        console.log(this.tempEvent.target)
        this.isDragging = false
      }*/
      console.log("mouseUp");
      this.is_attack_down = false;
      this.is_decay_down = false;
      this.is_sustain_down = false;
      this.is_release_down = false;
    },
    attack_down() {
      if (!this.is_attack_down) this.is_attack_down = true;
    },
    decay_down() {
      if (!this.is_decay_down) this.is_decay_down = true;
    },
    sustain_down() {
      if (!this.is_sustain_down) this.is_sustain_down = true;
    },
    release_down() {
      if (!this.is_release_down) this.is_release_down = true;
    },
  },
  computed: {
    adsr() {
      let attack = this.parameters.attack;
      let decay = this.parameters.decay;
      let release = this.parameters.release;
      let sustain = 0.2;
      let len_a = (attack / duration) * (100 - sustain / duration);
      let len_d = (decay / duration) * (100 - sustain / duration);
      let len_r = (release / duration) * (100 - sustain / duration);
      let len_b =
        ((duration - (attack + sustain + decay + release)) / duration) * 100;

      return {
        display: "grid",
        gridTemplateColumns:
          +len_a.toFixed(1) +
          "% " +
          len_d.toFixed(1) +
          "% " +
          (sustain / duration) * 100 +
          "% " +
          len_r.toFixed(1) +
          "0% " +
          len_b.toFixed(2) +
          "% ",
        gridTemplateRows: "88% 12%",
      };
    },
    attack() {
      return {
        height: "100%",
        width: "101%",
        clipPath: "polygon(0% 100%, 100% 0%, 100% 100%)",
      };
    },
    attack_Color() {
      return {
        height: "100%",
        background: colors.red.dark,
      };
    },
    decay() {
      return {
        display: "grid",
        gridTemplateRows:
          (1 - this.parameters.sustain) * 100 +
          "% " +
          this.parameters.sustain * 100 +
          "%",
        height: "100%",
      };
    },
    decay_head() {
      return {
        background: colors.red.medium,
        clipPath: "polygon(0% 0%, 100% 100%, 0% 100%)",
        height: "101%",
        width: "100%",
      };
    },
    decay_Color() {
      return {
        height: "100%",
        background: colors.red.medium,
      };
    },
    sustain() {
      return {
        display: "grid",
        gridTemplateRows:
          (1 - this.parameters.sustain) * 100 +
          "% " +
          this.parameters.sustain * 100 +
          "%",
        height: "100%",
      };
    },
    sustain_head() {
      return {
        background: "transparent",
      };
    },
    sustain_Color() {
      return {
        height: "100%",
        background: colors.red.light,
      };
    },
    release() {
      return {
        height: "100%",
        clipPath:
          "polygon(0% " +
          (1 - this.parameters.sustain) * 100 +
          "%, 0% 100%, 100% 100%)",
      };
    },
    release_Color() {
      return {
        height: "100%",
        background: colors.red.glow,
      };
    },
    space() {
      return {
        background: "transparent",
      };
    },
    attack_area() {
      let attack = this.parameters.attack;
      let sustain = 0.2;
      let len_a = (attack / duration) * 100 - sustain / duration;
      return {
        background: "transparent",
        aspectRatio: "1/1",
        width: "10%",
        position: "absolute",
        zIndex: "2",
        borderRadius: "50%",
        left: len_a - 5 + "%",
        clipPath: "circle(80%)",
      };
    },
    decay_area() {
      let attack = this.parameters.attack;
      let decay = this.parameters.decay;
      let sustain = 0.2;
      let len_a = (attack / duration) * (100 - sustain / duration);
      let len_d = (decay / duration) * (100 - sustain / duration);
      return {
        background: "transparent",
        aspectRatio: "1/1",
        width: "10%",
        position: "absolute",
        zIndex: "2",
        borderRadius: "50%",
        left: len_d + len_a - 5 + "%",
        top: (1 - this.parameters.sustain) * 100 - 20 + "%",
      };
    },
    sustain_area() {
      let attack = this.parameters.attack;
      let decay = this.parameters.decay;
      let release = this.parameters.release;
      let sustain = 0.2;
      let len_a = (attack / duration) * (100 - sustain / duration);
      let len_d = (decay / duration) * (100 - sustain / duration);
      let len_r = (release / duration) * (100 - sustain / duration);
      return {
        background: "transparent",
        aspectRatio: "16/9",
        width: "10%",
        position: "absolute",
        zIndex: "2",
        borderRadius: "50%",
        left: len_a + len_d + (sustain / duration) * 100 - 5 + "%",
        top: (1 - this.parameters.sustain) * 100 - 12 + "%",
      };
    },
    release_area() {
      let attack = this.parameters.attack;
      let decay = this.parameters.decay;
      let release = this.parameters.release;
      let sustain = 0.2;
      let len_a = (attack / duration) * (100 - sustain / duration);
      let len_d = (decay / duration) * (100 - sustain / duration);
      let len_r = (release / duration) * (100 - sustain / duration);

      return {
        background: "transparent",
        aspectRatio: "1/1",
        width: "10%",
        position: "absolute",
        zIndex: "2",
        borderRadius: "50%",
        left: len_a + len_d + (sustain / duration) * 100 + len_r - 5 + "%",
        top: "70%",
      };
    },
    attack_dot() {
      let attack = this.parameters.attack;
      let sustain = 0.2;
      let len_a = (attack / duration) * 100 - sustain / duration;
      return {
        background: colors.red.dark,
        aspectRatio: "1/1",
        width: "2%",
        position: "absolute",
        zIndex: "2",
        borderRadius: "50%",
        left: len_a - 1 + "%",
        top: "-2%",
      };
    },
    decay_dot() {
      let attack = this.parameters.attack;
      let decay = this.parameters.decay;
      let sustain = 0.2;
      let len_a = (attack / duration) * (100 - sustain / duration);
      let len_d = (decay / duration) * (100 - sustain / duration);
      return {
        background: colors.red.medium,
        aspectRatio: "1/1",
        width: "2%",
        position: "absolute",
        zIndex: "2",
        borderRadius: "50%",
        left: len_d + len_a - 1 + "%",
        top: (1 - this.parameters.sustain) * 88 - 2 + "%",
      };
    },
    sustain_line() {
      let attack = this.parameters.attack;
      let decay = this.parameters.decay;
      let release = this.parameters.release;
      let sustain = 0.2;
      let len_a = (attack / duration) * (100 - sustain / duration);
      let len_d = (decay / duration) * (100 - sustain / duration);
      let len_r = (release / duration) * (100 - sustain / duration);
      return {
        background: colors.gray.dark,
        aspectRatio: "16/9",
        width: "1.8%",
        aspectRatio: "1/1",
        position: "absolute",
        zIndex: "2",
        borderRadius: "50%",
        left: len_a + len_d + (sustain / duration) * 100 - 5 + "%",
        top: (1 - this.parameters.sustain) * 88 - 2 + "%",
      };
    },
    release_dot() {
      let attack = this.parameters.attack;
      let decay = this.parameters.decay;
      let release = this.parameters.release;
      let sustain = 0.2;
      let len_a = (attack / duration) * (100 - sustain / duration);
      let len_d = (decay / duration) * (100 - sustain / duration);
      let len_r = (release / duration) * (100 - sustain / duration);

      return {
        background: colors.red.glow,
        aspectRatio: "1/1",
        width: "2%",
        position: "absolute",
        zIndex: "2",
        borderRadius: "50%",
        left: len_a + len_d + (sustain / duration) * 100 + len_r - 1 + "%",
        top: "84%",
      };
    },

    time_Stretch() {
      return {};
    },
  },
};
</script>

<style scoped>
.envelope_container {
  position: absolute;
  height: 9.6vw;
  width: 29.25vw;
  top: 0.5vw;
  left: 0.5vw;
}

.container {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.envelope {
  width: 100%;
  height: 100%;
  background-color: transparent;
  box-sizing: border-box;
}

.knobs {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: white;
  box-sizing: border-box;
  display: grid;
  left: 29.25vw;
  width: 17.5vw;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
}

.knob {
  width: 100%;
  height: 100%;
  border: 0.1vw solid black;
  border-radius: 0.5vw;
  box-sizing: border-box;
}

.envHost {
  grid-column: span 2;
  /* takes 2 spaces horizontally*/
  background-color: #9bf6ff;
  position: relative;
}

.env_background {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.env_canvas {
  height: 0;
  width: 0;
  left: 1%;
  background-color: #cdfbff;
  border-radius: 0.2vw;
  border: 0.1vw solid #141f20;
  box-sizing: border-box;
}
</style>