<template>
  <div class="filter">
    <div class="filter_back" ref="filter_back">
      <div class="filter_logo" @mousedown="logoDown">
        <div v-if="is_logoDown && type != 'peaking'" class="logo_text">
          {{ ((1 - this.resonance) * 100).toFixed() + "%" }}
        </div>
        <div v-if="is_logoDown && type == 'peaking'" class="logo_text">
          {{ ((1 - 2 * this.resonance) * 100).toFixed() + "%" }}
        </div>
        <!--<div v-if="type==='peaking'" class="logo_BD_mask" :style="computedBDMaks"></div> -->
        <div class="logo_glow" :style="computedGlow"></div>

        <div class="logo_border"></div>
        <div class="logo">
          <div v-if="is_logoDown" class="logo_text_back"></div>
          <div
            v-if="type === 'lowpass'"
            class="logo_LP"
            :style="computedLP"
          ></div>
          <div
            v-if="type === 'highpass'"
            class="logo_HP"
            :style="computedHP"
          ></div>
          <div v-if="type === 'peaking'" class="logo_BD_back"></div>
          <div
            v-if="type === 'peaking'"
            class="logo_BD_circle"
            :style="computedBDCircle"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as Tone from "tone";

export default {
  props: ["filter_type", "chainId", "chainColor", "id"],

  data() {
    return {
      type: this.filter_type,
      cutoff: 0.5,
      resonance: 0.5,
      is_logoDown: false,
    };
  },

  mounted() {
    // mouse event listeners
    document.addEventListener("mousemove", this.onMouseMove);
    document.addEventListener("mouseup", this.onMouseUp);

    //console.log(this.$props)

    // -------------- AUDIO ---------------
    this.filter = new Tone.Filter();
    this.initFilterParams();

    //console.log(this.chainId+" "+this.id)
    //Inserting filter in global variable
    this.filterChains[this.chainId][this.id] = this.filter;

    if (
      this.id === this.nFiltersXchain - 1 &&
      this.chainId === this.nFilterChains - 1
    )
      this.$emit("filtersMounted");
  },

  methods: {
    // ----------- USER EVENTS -----------
    onMouseMove(event) {
      if (this.is_logoDown) {
        if (event.movementY < 0) {
          // Mouse is moving up
          this.resonance = Math.max(this.resonance + event.movementY / 100, 0);
          console.log("moving up");
        } else {
          // Mouse is moving down
          this.resonance = Math.min(this.resonance + event.movementY / 100, 1);
        }
        if (event.movementX < 0) {
          // Mouse is moving right
          this.cutoff = Math.max(
            this.cutoff + Math.floor(event.movementX) / 100,
            0
          );
        } else if (event.movementX > 0) {
          // Mouse is moving left
          this.cutoff = Math.min(
            this.cutoff + Math.floor(event.movementX) / 100,
            1
          );
        }
        // ----------- audio ---------
        this.setResonance();
        this.setCutoff();
      }
    },

    onMouseUp() {
      if (this.is_logoDown) this.is_logoDown = false;
      document.body.style.cursor = "pointer";
    },
    logoDown() {
      if (!this.is_logoDown) {
        this.is_logoDown = true;
        document.body.style.cursor = "none";
      }
    },

    // ----------- AUDIO HELPERS--------------
    // takes value in [0, 1] range and it maps it to [50, 16000] interval in a logarithmic fashion
    cutoffLogMapping(x) {
      if (x < 0 || x > 1) {
        console.error("Input value must be between 0 and 1.");
        return;
      }
      return 20 * Math.pow(10, Math.log10(22000 / 20) * x);
    },

    // takes value in [0, 1] range and it maps it to [0, 10] linearly
    resonanceRangeNormalizer(x, type) {
      if (x < 0 || x > 1) {
        console.error("Input value must be between 0 and 1.");
        return;
      }
      switch (type) {
        case "lowpass":
          return 15 * x;
        case "highpass":
          return 15 * x;
        case "peaking":
          return 30 * (x - 0.5);
        default:
          break;
      }
    },

    // ----------- AUDIO SETTERS--------------
    initFilterParams() {
      let self = this;

      // init switched off filters
      switch (this.filter_type) {
        case "lowpass":
          this.cutoff = 1;
          this.resonance = 1;
          break;
        case "highpass":
          this.cutoff = 0;
          this.resonance = 1;
          break;
        case "peaking":
          this.cutoff = 0.5;
          this.resonance = 0.5;
          break;
        case "bandpass":
          break;
        case "lowshelf":
          break;
        case "highshelf":
          break;
        case "notch":
          break;
        case "allpass":
          break;
        default:
          console.log("Invalid filter type");
      }

      this.filter.set({
        frequency: self.cutoffLogMapping(this.cutoff),
        type: this.filter_type,
      });
    },

    setCutoff() {
      let self = this;
      this.filter.set({
        frequency: self.cutoffLogMapping(this.cutoff),
      });
      console.log(
        this.type +
          " cutoff: " +
          this.cutoff.toFixed(2) +
          " - " +
          this.cutoffLogMapping(this.cutoff).toFixed(0) +
          " Hz"
      );
    },

    setResonance() {
      let self = this;

      if (
        this.type === "lowshelf" ||
        this.type === "highshelf" ||
        this.type === "peaking"
      ) {
        // bell delta amplitude is controlled changing the 'gain' parameter
        this.filter.set({
          gain: self.resonanceRangeNormalizer(1 - self.resonance, this.type),
        });
        console.log(
          "Setting Peaking Resonance: " +
            self.resonanceRangeNormalizer(1 - self.resonance, this.type)
        );
      } else if (this.type === "lowpass" || this.type === "highpass") {
        // filter's Q is controlled changing the 'Q' parameter 
        this.filter.set({
          Q: self.resonanceRangeNormalizer(1 - self.resonance, this.type),
        });
        console.log(
          "Setting LowPass/HighPass Resonance: " +
            self.resonanceRangeNormalizer(1 - self.resonance, this.type)
        );
      }

      /*
      console.log(
        "resonance: " +
          this.resonance.toFixed(2) +
          " - " +
          Math.round(this.resonanceRangeNormalizer((self.resonance), this.type)) +
          " dB"
      );
      */
    },

    connect(destination) {
      this.filter.connect(destination);
    },

    // ------------ GETTERS SETTERS -------------
    getFilter() {
      return this.filter;
    },
  },

  computed: {
    computedHeadStyle() {
      return {
        left: 0.7 * this.cutoff * 100 + 13.5 + "%",
      };
    },
    computedGlow() {
      if (this.filter_type == "peaking" && this.resonance < 0.5) {
        return {
          opacity: 1 - 2 * this.resonance,
          background: "#e58138",
        };
      } else if (this.filter_type == "peaking" && this.resonance >= 0.5) {
        return {
          opacity: 2 * this.resonance - 1,
          background: "#e58138",
        };
      } else {
        return {
          opacity: 1 - this.resonance,
        };
      }
    },
    computedLP() {
      return {
        transform: "translateX(" + (-100 + this.cutoff * 100) + "%)",
      };
    },
    computedHP() {
      return {
        transform: "translateX(" + this.cutoff * 100 + "%)",
      };
    },
    computedBDMaks() {
      let clip = "";
      if (this.resonance > 0.5)
        clip = "polygon(0% 0%, 100% 0%, 100% 50%, 0% 50%)";
      else if (this.resonance < 0.5)
        clip = "polygon(0% 50%, 0% 100%, 100% 100%, 100% 50%)";
      else clip = "polygon(0% 0%, 100% 0%, 100% 50%, 0% 50%)";
      return {
        clipPath: clip,
      };
    },
    computedBD() {
      if (1 - this.resonance >= 0.5) {
        return {
          //transform: 'translateY(' + (this.resonance > .5 ? -100 + this.resonance * 100 : this.resonance * 100) + '%)',
          height: (1 - this.resonance) * 100 + "%",
          transform: "translateX(" + this.cutoff * 100 + "%)",
        };
      } else {
        return {
          //transform: 'translateY(' + (this.resonance > .5 ? -100 + this.resonance * 100 : this.resonance * 100) + '%)',
          height: (1 - this.resonance - 0.5) * 100 + "%",
          transform: "translateX(" + (this.cutoff - 0.5) * 100 + "%)",
        };
      }
    },
    computedBDCircle() {
      return {
        transform:
          "translate(" +
          (this.cutoff - 0.5) * 100 +
          "%, " +
          (this.resonance - 0.5) * 100 +
          "%)",
        //transform: 'translateX('+(this.cutoff-.5)*100+'%)'
      };
    },
  },
};
</script>

<style scoped>
.filter {
  position: relative;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.filter_back {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 90%;
  background-color: transparent;
  justify-content: center;
  align-items: center;
}

.filter_logo {
  position: absolute;
  display: flex;
  flex: 5;
  background-color: #ffebd2;
  height: 80%;
  top: 0;
  aspect-ratio: 1/1;
  border-radius: 50%;
  z-index: 0;
  clip-path: circle(50%);
  justify-content: center;
  align-items: center;
}

.logo_border {
  position: absolute;
  height: 70%;
  aspect-ratio: 1/1;
  z-index: 5;
  border-radius: 50%;
  border: 0.1vw solid #3b210e;
}

.logo {
  position: absolute;
  height: 70%;
  aspect-ratio: 1/1;
  z-index: 3;
  clip-path: circle(50%);
  background-color: #e58138;
}

.logo_glow {
  position: absolute;
  height: 90%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background-color: #e58138;
  z-index: 1;
  filter: blur(0.5vw);
}

.logo_text {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1vw;
  font-weight: bold;
  position: absolute;
  height: 100%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  color: #ffebd2;
  opacity: 100%;
  background-color: #ffebd2;
  z-index: 5;
  background: rgba(255, 255, 255, 0.1);
}

.logo_text_back {
  position: absolute;
  z-index: 6;
  left: 15%;
  top: 30%;
  width: 70%;
  height: 40%;
  background-color: #e58138;
  opacity: 100%;
  border-radius: 10%;
}

.logo_LP {
  display: flex;
  position: absolute;
  height: 100%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background-color: #ffebd2;
  z-index: 1;
}

.logo_HP {
  display: flex;
  position: absolute;
  height: 100%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background-color: #ffebd2;
  z-index: 1;
}

.logo_BD_mask {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #ffebd2;
  z-index: 2;
}

.logo_BD {
  display: flex;
  position: absolute;
  height: 100%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background-color: #ffebd2;
  z-index: 1;
}
.logo_BD_line {
  position: absolute;
  width: 100%;
  height: 4%;
  top: 48%;
  filter: blur(0.02vw);
  border-radius: 30%;
  background-color: #e58138;
  z-index: 5;
}
.logo_BD_circle {
  position: absolute;
  width: 100%;
  aspect-ratio: 1/1;

  border-radius: 50%;
  background-color: #ffebd2;
  z-index: 5;
}
</style>