import {Gain, Reverb, Distortion, Vibrato, Panner, Chorus, PingPongDelay, Chebyshev, Envelope, LFO} from 'tone'

export default class MyEffect {
    constructor({ type = "none", modulation = 0 }) {
        this.type = type;
        this.effect = this.generateToneFx(type)
        this.mod = modulation;
        this.modulatorsPreset = {
            envelope: {
                vol: { amount: 1, signal: null },
                mod: { amount: 0, signal: null },
                pitch: { amount: 0, signal: null },
                pan: { amount: 0, signal: null },
            },
            lfo: {
                vol: { amount: 0, signal: null },
                mod: { amount: 0, signal: null },
                pitch: { amount: 0, signal: null },
                pan: { amount: 0, signal: null },
            }
        }
    }

    // getters
    getToneReference(){
        return this.effect;
    }

    // creator
    generateToneFx(type) {
        let effect;
        switch (type) {
            case 'none':
                effect = new Gain();
                break;
            case 'reverb':
                effect = this.createReverb()
                break;
            case 'delay':
                effect = this.createDelay()
                break;
            case 'distortion':
                effect = this.createDistortion()
                break;
            case 'vibrato':
                effect = this.createVibrato()
                break;
            case 'pan':
                effect = this.createPan()
                break;
            case 'chorus':
                effect = this.createChorus()
                break;
            default:
                effect = new Gain();
        }
        return effect;
    }

    // setters
    // general setter
    modulateFxParams({ fx = this.effect, type = this.effect, mod = this.mod }) {
        switch (type) {
            case 1: // reverb
                fx.set({
                    decay: 0.1 + 5*mod,
                    wet: 0.1 + 0.9*mod,
                })
                break;

            case 2: // delay
                fx.set({
                    delayTime: 0.4*mod,
                    feedback: 0.95*mod,
                    wet: 0.1 + 0.9*mod,
                })
                break;

            case 3: //  distortion
                fx.set({
                    distortion: 0.05 + 0.95*mod,
                    wet: 0.05 + 0.95*mod,
                })
                break;

            case 4: // vibrato
                fx.set({
                    depth: 0.5 + 0.5*mod,
                    frequency: 1 + 20*mod,
                    wet: 0.05 + 0.95*mod
                })
                break;

            case 5: // pan
                let coeff = -1 + (2*mod)
                fx.set({
                    pan: coeff,
                    wet: 1,
                })
                console.log(coeff)

                break;

            case 6: // chorus
                fx.set({
                    depth: 0.5 + mod*0.5,
                    freq: 2 + mod*8,
                    feedback: 0.05 + mod*0.9,
                    type: "triangle" + (mod*10).toString()
                })
                break;
            default:
                fx = new Gain();
        }
    }

    // -------------- FX CREATION -------------
    createReverb(){
        let reverb = new Reverb();
        reverb.set({
            decay: 5,
            wet: 0.9
        })
        return reverb;
    }
    createDelay() {
        //todo: not interesting effect if used like this
        let delay = new PingPongDelay();
        delay.set({
            delayTime: 0.15,
            feedback: 0.55,
            wet: 0.75,
        })
        return delay;
    }
    createDistortion(){

        let distortion = new Distortion();
        distortion.set({
            distortion: 0.8,
            wet: 0.5,
        })

        return distortion;
    }
    createVibrato(){
        let vibrato = new Vibrato();
        vibrato.set({
            depth: 0.85,
            frequency: 3,
            wet: 0.95
        })
        return vibrato;
    }
    createPan(){
        //const osc = new Oscillator(1, "sine").toDestination().start();
        // todo:
        let pan = new Panner();
        pan.set({
            pan: 0,
            wet: 1
        })
        return pan;
    }
    createChorus(){
        let chorus = new Chorus();
        chorus.set({
            depth: 1,
            frequency: 5,
            spread: 88,
            wet: 1,
            feedback: 0.6,
            delayTime: 220,
            type: "triangle6"
        })
        return chorus;
    }

    // TODO:  ------------- MODULATION ------------

    createModulator(type) {
        //console.log("creating modulator of type: "+type)
        switch (type) {
            case "envelope":
                return new Envelope;
            case "lfo":
                return new LFO;
            default:
                console.log("Error creating modulator");
                return null;
        }
    }

    modulatorSetter(modulator, type, parameters) {
        switch (type) {
            case "envelope":
                modulator.set({
                    "attack": parameters.attack,
                    "decay": parameters.decay,
                    "sustain": parameters.sustain,
                    "release": parameters.release
                })
                break;
            case "lfo":
                //TO-DO
                modulator.set({

                })
                break;
            default:
                console.log("Error setting modulator parameters, invalid args")
                break;
        }
    }
    setModulatorParameters(type, target, parameters) {
        if (this.initialized) {
            this.modulatorsData[type][target].parameters = parameters
            this.updateModulators()
        }
    }

    // have to pass, the modulator (tone), the type (lfo or envelope), the gain linked to the voice, the voice and the target
    connectModulator(modulator, type, gain, target) {
        switch (type) {
            case "envelope":

                break;

            case "lfo":
                //TO-DO
                break;

            default:
                console.log("invalid modulator type")
                break;
        }
    }


    // ------------- TONE JS EXTENSIONS -----------
    connect(route){
        this.effect.connect(route);
    }
    disconnect(){
        this.effect.disconnect();
    }
    dispose(){
        this.effect.dispose();
    }
}
