<template>
  <div class="oscHost">
    <div class="scrollable">
      <template v-for="(osc) in this.oscList" :key="osc.id" :item="osc">
        <Osc
          :ref="osc.ref"
          :id="osc.id"
          @disposed="removeOsc"
          @claim="handleClaim"
        />
      </template>
      <div class="oscVoid" @click="addOsc">+</div>
      <div v-if="this.oscList.length <= 1" class="oscVoid" @click="addOsc">+</div>
    </div>
  </div>
</template>

<script>
import Osc from "./Osc.vue";
import Vue from "vue";
import { isProxy, toRaw } from "vue";

export default {
  components: {
    Osc,
  },

  data() {
    // data structure oscillators list
    return {
      oscList: [],
      name: "oscHost",
      i: 0,
    };
  },

  mounted() {
    this.addOsc();
    this.addOsc();
  },

  methods: {
    initConnections() {
      for (let key in this.$refs) {
        //console.log(toRaw(this.$refs[key][0]))
        toRaw(this.$refs[key][0]).initConnections()
        
      }
    },

    handleClaim(info) {
      //TO DELETE?

      //Handling claiming of a drop by oscillator
      //adding actual module to path

      info.path.unshift(this.name);
      //console.log(info.path)

      this.$emit("claim", info);
    },

    addOsc() {
      //TO-DO REWRITE
      let ref = "osc" + this.i;
      this.oscList.push({ id: this.i, ref: ref });
      console.log("adding osc" + this.i)
      console.log(this.oscList);
      // this.oscList
      this.i += 1;
    },

    removeOsc(id) {
      //TO-DO REWRITE
      console.log("removing oscillator" + id);
      // Remove the oscillator from the list
      let index = 0
      this.oscList.forEach(osc => {
        if(osc.id===id) index = this.oscList.indexOf(osc)
      });
      this.oscList.splice(index, 1);
    },
  },
  unmounted() {},
};
</script>

<style scoped>
.oscHost {
  background-color: #ffadad;
}

.scrollable {
  font-size: 2vw;
  justify-content: center;
  overflow-y: scroll;
  /* Add a vertical scrollbar */
  overflow-x: hidden;
  width: 100%;
  height: 100%;
}
.oscVoid {
  margin: 2.5%;
  border: 0.1vw solid black;
  height: 31.25%;
  width: 94%;
  justify-content: center;
  display: flex;
  align-items: center;
  font-size: 2vw;
}

/*  hide the scrollbar because cannot be resized in Chrome, Safari (breaks % layout)*/
.scrollable::-webkit-scrollbar {
  display: none;
}
</style>